    margin-bottom: 0px;
}
</style>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import {
    extend,
    ValidationObserver,
    ValidationProvider
} from 'vee-validate';

import Alert from '../../Common/Alert.vue';
import VueTimepicker from 'vue2-timepicker'
import {
    getSecondsFromDate,
    formatter
} from "@/assets/common/vue-common.js"
import moment from "moment"
extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    message: 'The {_field_} is required'

    ///computesRequired: true
});
extend('number', value => {
    if (value >= 0) {
    return true;
  }
});
export default defineComponent({
    name: "Request Time",
    components: {
        ValidationObserver,
        Alert,
        VueTimepicker,
        ValidationProvider
    },
    props: {
        manualData: {
            type: Object
        },
        store:{
            type:Object
        }
    },
    data() {
        return {
            startTime: '00:00',
            endTime: '00:00',
            date: moment().format("YYYY-MM-DD"),
            time: '00:00:00',
            clientOrPm:1,
            requestData: {
                title: '',
                comment: '',
                msgboardid: '',
                projectid: '',
                taskid: '',
                request_time_sec: '',
                file:'',
                clients:'',
                timeInHours:''
            }
        }

    },
    created: function () {
        console.log(this.manualData)
        this.requestData.projectid = this.manualData.projectId
        this.requestData.taskid = this.manualData.taskId
        this.requestData.msgboardid = this.manualData.MESSAGE_BOARD_ID
        this.time = formatter(this.manualData.totalHours)
        this.store.clientsGetFromProjectId(this.manualData.projectId)
        this.store.pmGetFromProjectId(this.manualData.projectId)
        this.clientsDropdownChange()
    },
    methods: {
       async clientsDropdownChange(){
        console.log("check",this.manualData.projectid)
        this.requestData.clients = []    
        },
        previewFiles(event) {
            console.log(event.target.files);
        },
        async submit() {
            this.requestData.request_time_sec = this.requestData.timeInHours * 60 *60
            await this.store.requestTime(this.requestData);
            if(this.store.getRequestTimeResponse.status=="Success"){
                this.$emit('close')
                this.$emit('closeUpdateModal')
            }
        
            //const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
            //this.store.getTimerListing1(url)

        },

    },
   
});
</script>
